details {
margin: 5px;
font-size: 18px;
}

details > * {
padding: .75rem;
}

summary {
font-size: 20px;
font-family: sans-serif;
font-weight: bold;
cursor: pointer;
position: relative;
}

summary::marker {
content: "+";
}

summary[open]::marker {
  content: "-";
}

summary::before {
content:"";
position:absolute;
inset: .50rem;
left: auto;
aspect-ratio: 1;
background: 
conic-gradient(from 90deg at 33% 33%, #0000 90deg, #fff 0) 
100% 100%/60% 60%;
clip-path: inset(1px);
}

details[open] summary::before {
background: linear-gradient(#fff 0 0) 50%/100% 25% no-repeat;
}

details[open] summary {
border-radius: 5px 5px 0 0;
}

details > div {
background: #ddd;
border-radius: 0 0 5px 5px;
}


